  background: #fe9600;
  position: fixed;
  z-index: 1031;
  top: 0;
  left: 0;
  width: 0;
  height: 3px;
}

#nprogress .peg {
  box-shadow: 0 0 10px #fe9600, 0 0 5px #fe9600;
}
