  display: block;
  text-align: center;
  padding: 0;
  margin: 10px auto;
}
.menu-list li {
  display: inline-block;
  margin: 6px;
  line-height: 36px;
  width: 36px;
  background-color: #f5f5f5;
  border-radius: 50%;
  cursor: pointer;
}
.skin-menu.show {
  -webkit-transform: scaleX(1);
          transform: scaleX(1);
}
.skin-menu {
  position: fixed;
  bottom: 50px;
  right: 10px;
  height: auto;
  width: 220px;
  border-radius: 5px;
  background-color: #fff;
  box-shadow: 0 1px 40px -8px rgba(0,0,0,.5);
  z-index: 100;
  font-family: Ubuntu,sans-serif;
  -webkit-transform: scaleX(0);
