.no-select {
    -webkit-touch-callout: none;
    -webkit-user-select: none;
    -khtml-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
}
#emotion-toggle {
    text-align: center;
    padding: 1.5rem 0;
    margin-bottom: 0;
    
}
.motion-switcher-table th:hover {
    background-color: rgba(245,245,245,.5);
    color: orange;
}
#emotion-toggle span, .emotion-box, .on-hover, .emotion-item, .emoji-item, .tieba-container span {
    /* cursor: url(https://cdn.jsdelivr.net/gh/moezx/cdn@3.1.9/img/Sakura/cursor/No_Disponible.cur),auto; */
    cursor: pointer;
}
.emotion-box {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: horizontal;
    -webkit-box-direction: normal;
    -ms-flex-direction: row;
    flex-direction: row;
    -ms-flex-wrap: wrap;
    flex-wrap: wrap;
    display: none;
}
.motion-switcher-table {
    border-collapse: collapse;
    width: 100%;
    margin: 0;
    table-layout: fixed;
}

.motion-switcher-table .on-hover {
    color: orange;
}
.motion-switcher-table th, .motion-switcher-table td {
    padding: 8px;
    text-align: center;
    border-radius: 5px;
}
#emotion-toggle, .emotion-box, .on-hover, .emotion-item, .emoji-item, .tieba-container span {
    /* cursor: url(https://cdn.jsdelivr.net/gh/moezx/cdn@3.1.9/img/Sakura/cursor/No_Disponible.cur),auto; */
}
.motion-container {
    height: 110px;
    overflow: auto;
    margin-bottom: 5px;
    border-radius: 5px;
}
.bili-container, .tieba-container {
    padding-left: 16px;
}

.emotion-select-parent {
    background-size: cover;
}
.emotion-secter {
    margin: 12px 12px 0 0;
}
.emotion-item {
    display: inline-block;
    width: 32px;
    height: 32px;
    overflow: hidden;
    border-radius: 4px;
}
.emotion-item .img {
    width: 32px;
    height: 864px;
    max-width: 32px;
    background: top/32px no-repeat;
    background-image: none;
}
.emotion-item .img {
    -webkit-animation: im-emotion-step 1.08s steps(27) infinite;
    animation: im-emotion-step 1.08s steps(27) infinite;
    animation-duration: 1.08s;
    animation-timing-function: steps(27);
}
.emotion-select-child {
    display: none;
}
.emotion-select-parent:hover {
    background-image: none !important;
    background-color: rgba(245,245,245,.8);
    border-radius: 5px;
}

.emotion-select-parent:hover .emotion-select-child {
    display: block;
}
.motion-container {
    height: 110px;
    overflow: auto;
    margin-bottom: 5px;
    border-radius: 5px;
}

.tieba-container span {
    width: 32px;
    height: 32px;
}
.tieba-container img {
    width: 32px;
    height: 32px;
    margin: 7px;
}
.dark-mode .menhera-container .emoji-item{
    color: #fff;
}

.dark-mode .menhera-container .emoji-item:hover{
    color: #333;
}
.menhera-container .emoji-item {
    padding: 3px 5px;
    margin: 3px;
    display: inline-block;
    border-radius: 4px;
    color: #333;
}
.menhera-container .emoji-item:hover {
    background-color: rgba(245,245,245,.8);
}
@media (max-width: 780px){
    #emotion-toggle {
        padding: 1rem 0;
    }
}



@keyframes im-emotion-step{0%{-webkit-transform:translateY(0);transform:translateY(0)}to{-webkit-transform:translateY(-100%);transform:translateY(-100%)}}